<template>
	<div id="ptheader">
		<slot></slot>
		<div class="back" @click="backbefore()"><i class="icon iconfont icon-jiantou3"></i></div>
	</div>

</template>

<script>
export default {
	methods:{
		backbefore(){
			this.$router.go(-1)
		}
	}
}
</script>

<style>
	#ptheader{
		width: 100%;
		height: 10vh;
		background:linear-gradient(to right, #0b752b,#0b752b);
		font-size: 1.4rem;
		text-align: center;
		line-height: 10vh;
		color: #fff;
		position: relative;
	}
	.back {
		position: absolute;
		margin-left: 1%;
		width: 20px;
		top: 0;
	}
</style>



