<template>
	<div id="ig">
		<ptheader></ptheader>
		<div class="ig_d1">

      <div  @click="proKindChoose">
        <label>*:</label>
        <!--		<select name="" id="" v-model="ig_class">
                      <option v-for="item in iglist" :value="item.key">{{item.value}}</option>
                  </select>
      -->


        <input type="hidden" v-model="ig_class"/>
        <input type="text" v-model="ig_Value" placeholder=""  readonly/>
      </div>


      <mt-popup
        v-model="projectkindshow"
        position="bottom">
        <ul class="wg_d1_ul2 percentul">
          <li class="cancel"></li>
          <li @click="proKind(item.key,item.value)"  v-for="item in iglist">{{item.value}}</li>
        </ul>
      </mt-popup>

      <label>*:</label>
			<input v-model="ighappentime" type="text" readonly @click="setigDate()" placeholder="">

			<label for="">:</label>
			<!-- <textarea v-model="igadd"></textarea>-->
      <contentedit v-model='igadd'></contentedit>
    	<label for=""></label>







			<!--<div class="sup_din1">
				+
				<input type="file" id="sup_in1" accept="image/*" @change='uploadigimg($event)'>
			</div>-->





    <attachment @attachmentsID="attachmentsID" :copyuploadURLs="copyuploadURLs" :attachments="attachments" v-if="!browser"></attachment>



      <div class="com-upload-img" v-if="browser">
        <div class="img_group">
          <div class="img_box filePic" v-if="allowAddImg">+
            <input type="file" class="fileImg" accept="image/*" multiple="multiple" @change="changeImg($event)">
            <div class="filter"></div>
          </div>

        </div>
        <div class="img_box" v-for="(item,index) in imgArr"  style="z-index:99999;">
          <div class="img_show_box">
            <img :src="item" alt="" class="imgLi">
           </div>
          <i class="img_delete" @click="deleteImg(index)">x</i>
        </div>
      </div>






<!--
			<div id="previewimg">
				<div class="preview" v-for="(item,index) in upfilename">
					<div class="previegiveup" @click="removeimg(index)">X</div>
					<img :src="item">
				</div>
			</div>-->

			<label>:</label>

    	 <input v-model="igadderss" type="text" id="address">


			<label>:</label>
		  <!--<input v-model="igcon" type="text">-->
     <contentedit v-model='igcon'></contentedit>








			<label for="">():</label>
			<input v-model="igcheckp" type="text">

			<div class="igsub" @click="pgsub()"></div>
			<div class="igtemporary" @click="saveFile()"></div>

		</div>

		<wait id="ig_wait" v-show="waitforamin"></wait>


  </div>
</template>

<script>
import ptheader from "@/components/header/ptheader";
import wait from "@/components/wait";
import attachment from "@/components/attachment";
import contentedit from "@/components/contentedit";
import axios from 'axios'
import { MessageBox } from 'mint-ui';



export default {
	data(){
		return{
			waitforamin:false,
      projectkindshow:false,
			iglist:[],
			ighappentime:"",
      ig_Value:"",
			igadd:"",
			ig_class:'',
			igadderss:"",
			igcon:"",
			igcheckp:"",
			sid:"",
			filename:[],
			upfilename:[],
      attachmentses: [],
      copyuploadURLs:[],
      attachments:[],
      imgData:'',
      imgArr:[],
      imgSrc:'',
      allowAddImg:true,
      fileName:[],
      baseArr:[],
      browser:true
     }
	},
	components:{
		ptheader,wait,attachment,contentedit
	},
  watch: {
    igadd() {
      var divhidden = document.getElementById("divhidden");
      var textareas = document.getElementById("textareas");
      var val = textareas.value;
      divhidden.innerHTML = "<pre style='width:10rem;white-space: pre-wrap;word-wrap: break-word;'>" + val + " " + "</pre>";
      textareas.style.height = (divhidden.offsetHeight - 2) + "px";
      if (val == "") {
        textareas.value = " ";
      }
    }
  },
	methods:{
    changeImg: function(e) {
      var _this = this;
      var imgLimit = 1024;
      var files = e.target.files;

      var image = new Image();
      if(files.length>0){
        var dd = 0;
        var timer = setInterval(function(){
          if(files.item(dd).type != 'image/png'&&files.item(dd).type != 'image/jpeg'&&files.item(dd).type != 'image/gif'){
            return false;
          }

          if(files.item(dd).size>imgLimit*102400){
            //to do sth
          }else{
            let arrName = [];
            for(var i=0;i<files.length;i++) {
              arrName.push(files[i].name);
            };

            image.src = window.URL.createObjectURL(files.item(dd));
            image.onload = function(){
              // 
              var w = image.width,
                h = image.height,
                scale = w / h;
              w = 200;
              h = w / scale;
              // 0.7quality
              var quality = 0.7;
              //canvas
              var canvas = document.createElement('canvas');
              var ctx = canvas.getContext('2d');
              // 
              var anw = document.createAttribute("width");
              anw.nodeValue = w;
              var anh = document.createAttribute("height");
              anh.nodeValue = h;
              canvas.setAttributeNode(anw);
              canvas.setAttributeNode(anh);
              ctx.drawImage(image, 0, 0, w, h);
              var ext = image.src.substring(image.src.lastIndexOf(".")+1).toLowerCase();//
              var base64 = canvas.toDataURL("image/"+ext, quality);


              let Token = localStorage.getItem('token');
              let URLPth;
              var Net= localStorage.getItem('INTERNET');
              if(Net == 1) {
                URLPth =  process.env.INSIDE_ROOT;
              }else if(Net == 2){
                URLPth=  process.env.OUTSIDE_ROOT;
              };
              var params = new FormData();
              params.append('token', Token);
              params.append('imageName', arrName);
              params.append('imageStr', base64.split(",")[1]);


              _this.$http({
                method: 'post',
                url: URLPth+'/common/upload/images',
                data:params
              }).then( res => {
                  _this.attachmentses.push(res.id);
               });

              // base64
              if(_this.imgArr.length<=4){
                _this.imgArr.unshift('');
                _this.imgArr.splice(0, 1, base64);//this.imgArr[index] = url;
                if(_this.imgArr.length>=5){
                  _this.allowAddImg = false;
                }
              }
            };



          }

          if(dd<files.length-1){
            dd++;
          }else{
            clearInterval(timer);
          }
        },1000)
      }
    },
    deleteImg: function(index){
      this.imgArr.splice(index,1);

      this.attachmentses.splice(index,1);

      if(this.imgArr.length<5){
        this.allowAddImg = true;
      }
    },
    getUrlKey: function (name) {
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null;
    },

    proKindChoose() {
      this.projectkindshow = true;
     },
    proKind(key,value) {
      this.projectkindshow = false;
      this.ig_class = key;
      this.ig_Value = value;
    },
    attachmentsID(childValue) {
      this.attachmentses = childValue.join();
    },
		removeimg(index){
			this.upfilename.splice(index,1);
			this.filename.splice(index,1);
		},
		getBase64Image(imgElem){
			return imgElem.replace(/^data:image\/(jpeg|jpg|png);base64,/, "");
		},
		uploadigimg($event){
			let _this=this;
			var input = $event.target;
			if(_this.filename.length<5){
				var reader = new FileReader();
				reader.onload = function () {
					var txt = event.target.result;
					_this.filename = _this.filename.concat(_this.getBase64Image(txt));
					_this.upfilename = _this.upfilename.concat(txt);
				};
				reader.readAsDataURL(input.files[0]);
			}else{
				MessageBox.alert('5').then(action => {
				}).catch(action => {
				})
			}
		},

		setigDate(){
			this.$calendar.show({
				onOk:(date) =>{
					this.ighappentime= date
				}
			});
		},
    pgsub() {
      var _this = this;
      var params = new FormData();
      var vConsole = new VConsole();
      console.log(111);
      console.log(this.attachmentses);
      params.append('dictCode', this.ig_class);
      params.append('happenTime', this.ighappentime);
      params.append('content', this.igadd);
      params.append('place', this.igadderss);
      params.append('checks', this.igcon);
      params.append('checkPeople', this.igcheckp);
      params.append('attachments', this.attachmentses);
      this.$http({
        method: 'post',
        url: '/prizepunish/save/submit',
        data:params
      }).then(res => {
         MessageBox.alert('!').then(action => {
            _this.$router.push(_this.$route.query.redirect || '/home/wg');
          })
      }).catch(err => {
        MessageBox.alert(',!').then(action => {
          _this.$router.push(_this.$route.query.redirect || '/home/wg');
        });
      });
    },
    saveFile() {
      var _this = this;
      var params = new FormData();
      params.append('dictCode', this.ig_class);
      params.append('happenTime', this.ighappentime);
      params.append('content', this.igadd);
      params.append('place', this.igadderss);
      params.append('checks', this.igcon);
      params.append('checkPeople', this.igcheckp);
      params.append('attachments', this.attachmentses);
       this.$http({
        method: 'post',
        url: '/prizepunish/save/temporary',
        data:params
      }).then(res => {

         MessageBox.alert('!').then(action => {
           _this.$router.push(_this.$route.query.redirect || '/home/wg');
         });
      });
    },
	  init() {
      let _this = this;
      let params = new URLSearchParams();
      params.append('code', 'event_type');
      this.$http({
        method: 'post',
        url: '/common/constant',
        data: params
      }).then(function (res) {
        _this.iglist = res.event_type;
      });
    }
	},
	created(){
		this.init();
		var ddd= JSON.parse(localStorage.ccc);
		this.sid = ddd.id;
		let _this = this;
    if(window.plus){
      _this.browser = false;
    }
  }
}
</script>

<style>
#ig .ig_d1{
	margin: 3rem 3% 0 3%;
}

#ig input{
	font-size: 1.1rem;
}

#ig select{
	font-size: 1.1rem;
}

#ig label{
	font-size: 1.1rem;
}


#ig .ig_d1 label{
	display: block;
	margin: 2vh 0 ;
}


#ig .ig_d1 input{
	display: block;
	line-height: 2.5rem;
	border: 1px solid #e6e6e6;
	border-radius: 5px;
	width:95%;
	margin: 0 auto;
	padding-left: 2%;
  white-space:normal;
  word-wrap:break-word;
  word-break:break-all;
}
#ig .ig_d1 select{
	display: block;
	height: 2.5rem;
	line-height: 2.5rem;
	border: 1px solid #e6e6e6;
	border-radius: 5px;
	width:95%;
	margin: 0 auto;
	padding-left: 2%;
}

#ig .igsub{
	width: 90%;
	margin: 0 auto;
	text-align: center;
	margin-top: 2rem;
	height: 3rem;
	line-height: 3rem;
	font-size: 1.3rem;
	background-color: #0b752b;
	color: #fff;
	border-radius: 5px;
}
#ig .igtemporary{
	width: 90%;
	margin: 0 auto;
	text-align: center;
	margin-top: 2rem;
	height: 3rem;
	line-height: 3rem;
	font-size: 1.3rem;
	background-color: #d2d2d2;
	color: #111;
	border-radius: 5px;
	margin-bottom: 5vh;
}
#ig textarea{
	width: 95%;
	resize: none;
	display: inline-block;
	margin-left:2.5%;
	height:7rem;
}

.addfile{
	line-height: 2.5rem;
}

#previewimg{
	width: 100%;
	margin-top: 2rem;
}
#previewimg .preview{
	width:30%;
	display: inline-block;
	height: 13vh;
	margin-right:3%;
	margin-bottom: 2vh;
	position: relative;
}
.previegiveup{
	position: absolute;
	top: -2vw;
	right: -2vw;
	text-align: center;
	border: 1px solid #999999;
	height: 5vw;
	width: 5vw;
	background-color: #e5e5e5;
	color: #999999;
	opacity: 0.5;

	border-radius: 100%;
}
#previewimg .preview img{
	width: 100%;
	height: 100%;
}

#ig .sup_din1{
	width: 15vw;
	height: 15vw;
	border: 1px dashed #e6e6e6;
	margin-top: 5vw;
	margin-left: 5%;
	position: relative;
	font-size:3rem;
	text-align: center;
	line-height:15vw;
	color:#e6e6e6;
}

#ig #sup_in1{
	display: inline-block;
	width: 100%;
	height: 100%;
	opacity: 0;
	position: absolute;
	left: 0 ;
	margin: 0;
	padding: 0 ;
	border: none;
}
#divhidden {
  visibility: hidden;
}
.mint-popup-bottom{
  width: 100%;
}
  .percentul{
    width: 100%;
    display: block;
    height: 38vh;
    overflow-y: scroll;
   }
  .percentul li{
    height: 4vh;
    line-height: 4vh;
    text-indent: 5vw;
  }
  .cancel{
    padding-right: 5vw;
    text-align: right;
    height: 4vh;
    line-height: 4vh;
  }

  .filePic{
    width: 60px;
    height: 60px;
    border: 1px dashed #e2e2e2;
    line-height: 53px;
    text-align: center;
    font-size: 40px;
    color: #e2e2e2;
  }

  .fileImg{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    outline: none;
    background-color: transparent;
    filter:alpha(opacity=0);
    -moz-opacity:0;
    -khtml-opacity: 0;
    opacity: 0;
  }



.imgLi {
  float: left;
  width:30vw;
  height:30vw;
  margin-right: 2px;
  position: relative;
  display: inline-block;
  margin-top: 20px;

}

.imgLi:last-child{
  margin-right: 0;
}



.imgLi:last-child{
  margin-right: 0;
}
.img_show_box{
  position: relative;
}



  .img_box{
    position: relative;
    display: inline-block;
    margin-right: 2vw;
  }
.img_box:nth-child(4){
  margin-right: 0px;
}
.img_delete{
  font-style: normal;
  border-radius: 50%;
  border: 1px solid #ffffff;
  width: 15px;
  height: 15px;
  line-height: 12px;
  text-align: center;
  position: absolute;
  z-index: 999999;
  top: 3vh;
  left: 15vh;
  color: #ffffff;
}

</style>



