<template>
	<div id="Wexamination1">
		<div>
			<div class="harticle_d_d1"><span></span><span class="hamore"><router-link to="/cg?id=notice&type=0">>></router-link></span></div>
			<ul>
				<li v-for="(item,index) in welist" class="Wexamination_li"  v-if="item.types ==0">
          <router-link :to="{path:'/home/maintext',query:{id:item.id,types:0}}">
						<div class="ellipsis">{{item.title}}</div>
						<div class="frontnotice_li_d2">{{item.releaseTime}}</div>
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
  export default {
	data(){
		return {
			welist:[],
      filterarr:[]
		}
	},
    created(){
	  let _this = this;
  	this.$http.get("/inforelease/app/info/index").then(res=>{
       _this.welist=res;

    })
	}
}
</script>





<style>
#Wexamination1{
	border-bottom: 1px solid #e6e6e6;
	border-top: 0;
	box-shadow: 0 5px 5px #e6e6e6;
	margin: 0 1% 3vh 1%;
}
.frontnotice_li_d2{
	text-align: right;
	color: #e6e6e6;
}
.Wexamination_li{
	font-size:1.1rem;
	margin:1rem 5%;
	height: 3rem;
}
.Wexamination_li a{
	display: flex;
	justify-content: space-between;
}
.Wexamination_li a:hover{
	color:#0b752b;
	display: flex;
	justify-content: space-between;
}
.ellipsis{
  width: 17rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  height: 2rem;
  float: left;
}
.frontnotice_li_d2{
  float: right;
}

</style>



