<template>
	<div id="Wexamination">
		<ptheader></ptheader>
    <div>
			<input style="display:none" type="text" readonly v-model="win3"/>
		</div>
		<div class="wtitle"></div>
  	<label></label>
		<div>
			<input readonly type="text" v-model="wtc" @click="wtcshow()" placeholder="">
		</div>
  	<label></label>
    <contentedit v-model='win4'></contentedit>
    <label></label>
    <contentedit v-model='win5'></contentedit>
    <label></label>
		<div>
			<select v-model="win6">
				<option v-for="item in wtcli111" :value="item.key">{{item.value}}</option>
			</select>
		</div>
   <label></label>
		<div>
			<input v-model="sdate" type="text" readonly="readonly" @click="setDate2()" placeholder=""/>
		</div>
   <label></label>
		<div>
			<input type="text" v-model="win14"/>
		</div>
  	<div class="wsub" @click="wsubmit()"></div>
		<div class="wtemporary" @click="wkeep()"></div>
		<div id="wtcbg" v-show="chosewtc"></div>
		<div class="wtc" v-show="chosewtc">
			<ul>
				<li v-for="item in wtcli" :value="item.cycleName" @click="cwtc(item)">
					<div><span class="wtc_span1">: </span><span>{{item.unitName}}</span></div>
					<div><span class="wtc_span1">: </span><span>{{item.cycleName}}</span></div>
					<div><span class="wtc_span1">: </span><span>{{changetime(item.assessStartTime)}}</span></div>
					<div><span class="wtc_span1">: </span><span>{{changetime(item.assessStartTime)}}</span></div>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
import ptheader from "@/components/header/ptheader";
import { MessageBox } from 'mint-ui';
import contentedit from "@/components/contentedit";

export default {
	data(){
		return {
			wbirthday:"",
			chosewtc:false,
			sdate:"",
			ddate:"",
			cdate:"",
			adate:"",
			wtcli:[],
			wtcliid:"",
			wtcli111:[],
			wtc:"",
      win1:"",
      win2:"",
      win3:"",
      win4:"",
      win5:"",
      win6:"",
      win7:"",
      win8:"",
      win9:"",
      win10:"",
      win11:"",
      win12:"",
      win13:"",
      win14:"",
      wid:'',
		}
	},

	components:{
		ptheader,contentedit
	},
	methods:{
    changetime(e){
      var newTime = new Date(e);
      var m = newTime.getMonth()+1
      var y = newTime.getFullYear()
      var d = newTime.getDate()
      if(m<10&&d<10){
        return y+"-0"+m+"-0"+d
      }
      if(m<10&d>9){
        return y+"-0"+m+"-"+d
      }
      if(m>9&d<10){
        return y+"-"+m+"-0"+d
      }
      else{
        return y+"-"+m+"-"+d
      }

    },
    setDate2(){
      this.$calendar.show({
        onOk:(date) =>{
          this.sdate= date
        }
      });
    },
    setDate3(){
      this.$calendar.show({
        onOk:(date) =>{
          this.ddate= date
        }
      });
    },
    setDate4(){
      this.$calendar.show({
        onOk:(date) =>{
          this.cdate= date
        }
      });
    },
    setDate5(){
      this.$calendar.show({
        onOk:(date) =>{
          this.adate= date
        }
      });
    },
		wsex(sex){
			if(sex == 1){
				return ""
			}
			if(sex == 2){
				return ""
			}
		},
    wtcshow(){
			this.chosewtc = true;
		},
		cwtc(item){
			this.chosewtc =false;
			this.wtc = item.cycleName;
			this.wtcliid= item.id
		},

		wsubmit(){

      var params = new FormData();

          params.append('workSummary', this.win4);
          params.append('selfAssOpinion', this.win5);
          params.append('selfAssGrade', this.win6);
          params.append('selfAssTime', this.sdate);
          params.append('assessedSign', this.win14);
          params.append('assessCycleId', this.wtcliid);

          this.$http({
            method: 'post',
            url: '/assess/cycle/personal/save/submit',
            data:params
          }).then(res=>{
            var _this=this;
            if(res.data.status == 0){
              MessageBox.alert("!").then(action => {
                _this.$router.push(_this.$route.query.redirect || '/home/rpublicity')
              })
            }else if(res.data.status == 1){
              MessageBox.alert("!").then(action => {
                _this.$router.push(_this.$route.query.redirect || '/home/rpublicity')
              })
            }
          })

		},

		wkeep(){
     var params = new FormData();
          params.append('workSummary', this.win4);
          params.append('selfAssOpinion', this.win5);
          params.append('selfAssGrade', this.win6);
          params.append('selfAssTime', this.sdate);
          params.append('assessedSign', this.win14);
          params.append('assessCycleId', this.wtcliid);


     this.$http({
        method: 'post',
        url: '/assess/cycle/personal/save/temporary',
        data:params
      }).then(res=>{
        var _this=this;
        if(res.data.status == 0){
          MessageBox.alert("!").then(action => {
            _this.$router.push(_this.$route.query.redirect || '/home/rpublicity')
          })
        }else if(res.data.status == 1){
          MessageBox.alert("!").then(action => {
            _this.$router.push(_this.$route.query.redirect || '/home/rpublicity')
          })
        }
        console.log(res)
      }).catch(err=>{
        console.log(err)
      })
  	},
    init() {
      let _this = this;
      let params = new URLSearchParams();
      params.append('code', 'AppAssGrades,operate-state');
      this.$http({
        method: 'post',
        url: '/common/constant',
        data: params
      }).then(function (res) {
         _this.wtcli111 = res.AppAssGrades;
       //   _this.wtcli = res['operate-state'];
      });
    }

	},
  created:function(){
     var ddd= JSON.parse(localStorage.ccc);
     this.wid = ddd.id;
     this.init();
     //
     this.$http.get("/assess/cycle/select/self").then(res => {
        this.wtcli = res.data;
      });
  }
}
</script>

<style scoped>
  textarea{
    border: 1px solid #e6e6e6;
  }
#Wexamination{
	font-size: 1.2rem;
	margin-bottom: 10vh;
}
#Wexamination label{
	line-height: 3rem;
	margin-left: 3%;
}
#Wexamination div input{
	width: 96%;
	margin-left: 2%;
	height: 2.5rem;
	line-height: 2.5rem;
	border: 1px solid #e6e6e6;
	border-radius: 5px;
	padding-left: 1%;
}

#Wexamination div select{
	width: 96%;
	margin-left: 2%;
	height: 2.5rem;
	line-height: 2.5rem;
	border: 1px solid #e6e6e6;
	border-radius: 5px;
}

#Wexamination textarea{
	width: 90%;
	resize: none;
	display: inline-block;
	margin-left:5%;
	height:7rem;
	padding: 0.3rem 1%;
}
#Wexamination .wd1{
	height: 3rem;
	line-height: 3rem;
	margin-top: 2vh;
	display: flex;
	padding: 0 5%;
	justify-content: space-between;
}
#Wexamination .wd1 label{
	margin-left: 0 ;
}
#Wexamination .wd1 span input{
	height: 1rem;
	width: 1rem;
}

.wsub{
	width: 90%;
	margin: 0 auto;
	text-align: center;
	margin-top: 2rem;
	height: 3rem;
	line-height: 3rem;
	font-size: 1.3rem;
	background-color: #0b752b;
	color: #fff;
	border-radius: 5px;
}
.wtemporary{
	width: 90%;
	margin: 0 auto;
	text-align: center;
	margin-top: 2rem;
	height: 3rem;
	line-height: 3rem;
	font-size: 1.3rem;
	background-color: #d2d2d2;
	color: #111;
	border-radius: 5px;
	margin-bottom: 5vh;
}
#Wexamination .wtc{
	position: absolute;
	top:10vh;
	left:5%;
	width: 90%;
	height: 80vh;
	z-index: 10000;

}
.wtc ul{
	overflow: scroll;
	max-height:75rem;
}
.wtc ul li{
	width: 90%;
	padding: 1vh 3%;
	margin: 3vh auto;
	box-shadow: 5px 5px 5px #e6e6e6;
	border: 1px solid #e6e6e6;
}
.wtc ul li:hover{
	width: 95%;
	padding: 1vh 3%;
	margin: 3vh auto;
	box-shadow: 5px 5px 5px #e6e6e6;
	color:#0b752b;
	border: 1px solid #0b752b;
}
.wtc ul li div{
	line-height: 3rem;
}


.wtc ul li div .wtc_span1{
	display: inline-block;
	width: 35%;
}


#wtcbg{
	position: absolute;
	top: 0;
	height: 100%;
	width: 100%;
	background-color: #e5e5e5;
	opacity: 0.9;
	z-index: 100;
}

.wtitle{
  width: 95%;
  margin: 0 auto;
  height: 1rem;
  line-height: 1rem;
  border-left: 5px solid #0b752b;
  text-indent: 1rem;
  margin-top: 2rem;

}
.wself{
	width: 100%;
	margin: 3vh 0;
	padding: 0 5%;
}
.wself span{
	display: inline-block;
	min-width: 20%;
}
</style>



