<template>
  <div id="attachment">
    <div class="attachment_div" @click="showActionSheet()">
      +
    </div>
    <ul class="van-overflow-hidden">
      <li class="imgLi aaaa" v-for="(item, index) in uploadURLs">
        <img :src='item' class="upload_img"/>
        <span @click="delImg(index,item,id)">
          <span class="file-remove">
            <img src="../assets/img/close.png">
          </span>
        </span>
      </li>
      <p v-for="item in copyimgIDs" style="display: none;">
        {{item}}
      </p>
      <li class="imgLi  bbb" v-for="(item, index) in copyuploadURLs">
     <!--  <img :src='`URLPth`+item.pelativePath' class="upload_img"/>-->
        <img v-bind:src="URLPth+file+(item.pelativePath)" class="upload_img"/>
        <span @click="delImg1(index,item.id)">
          <span class="file-remove">
           <img src="../assets/img/close.png">
          </span>
        </span>
     </li>
    </ul>
    <div style="clear: both"></div>
    <p v-for="(item,index) in imgIDs" style="display: none">
      {{item}}
    </p>
  </div>
</template>
<script>

  export default {
    name: 'navbar',
    data() {
      return {
        uploadURLs: [],
        imageFile: [],
        imageNames: [],
        imgIDs: [],
        URLPth:'',
        file:'',
        copyimgIDs:[]
      }
    },

    methods: {
      showActionSheet() {
        let _this = this;
        var bts = [{
          title: ""
        }, {
          title: ""
        }];
        plus.nativeUI.actionSheet({
            cancel: "",
            buttons: bts
          },
          function (e) {
            if (e.index == 1) {
              _this.getImage();
            } else if (e.index == 2) {
              _this.galleryImgs();
            }
          }
        );
      },
      getImage() {
        let _this = this;
        var cmr = plus.camera.getCamera();
        cmr.captureImage(function (p) {
          plus.io.resolveLocalFileSystemURL(p, function (entry) {
            _this.compressImage(entry.toLocalURL(), entry.name);
          }, function (e) {
            plus.nativeUI.toast("" + e.message);
          });
        }, function (e) {
        }, {
          filter: 'image'
        });
      },
      galleryImgs() {
        let _this = this;
        plus.gallery.pick(function (event) {
          var files = event.files;
          for (var i = 0; i < files.length; ++i) {
            var file = files[i];
            _this.compressImage(file, file.substring(file.lastIndexOf('/') + 1, file.length));
          }
        }, function (e) {
        }, {
          filter: "image",
          multiple: true,
          maximum: 4,
          system: false
        });
      },
      showPics(url, name) {
        let URLPth= process.env.INSIDE_ROOT;
        let _this = this;
        //
        plus.io.resolveLocalFileSystemURL(url, function (entry) {
          entry.file(function (file) {
            let fileReader = new plus.io.FileReader();
            fileReader.readAsDataURL(file);
            fileReader.onloadend = function (e) {
              if (_this.uploadURLs.length > 2) {
                plus.nativeUI.toast("3");
                return false;
              } else {
                let picUrl = e.target.result.toString();
                _this.uploadURLs.push(picUrl);//base64

                let Token = localStorage.getItem('token');



                let URLPth;



                var Net= localStorage.getItem('INTERNET');
                if(Net == 1) {
                  URLPth =  process.env.INSIDE_ROOT;
                }else if(Net == 2){
                 URLPth=  process.env.OUTSIDE_ROOT;
                };






                var params = new FormData();
                params.append('token', Token);
                params.append('imageName', name);
                params.append('imageStr', picUrl.split(",")[1]);


                _this.$http({
                  method: 'post',
                  url: URLPth+'/common/upload/images',
                  data:params
                }).then(res =>{

                  _this.imgIDs.push(res.id);
                  // _this.imgIDs.push(_this.copyimgIDs);
                  _this.imgIDs.push(_this.copyimgIDs);


                _this.$emit('attachmentsID', _this.imgIDs);
                  console.log( _this.imgIDs);
                }).catch( err => {
                  console.log(JSON.stringify(err));
                })




              }
            }
          });
        });
      },
      compressImage(url, filename) {
        let _this = this;
        var name = "_doc/upload/" + filename;
        plus.zip.compressImage({
            src: url,
            dst: name,
            quality: 40,
            overwrite: true
          },
          function (zip) {
            //
            _this.showPics(zip.target, filename);
            _this.imageNames.push(name);

          }, function (error) {
            plus.nativeUI.toast("");
          });
      },
      delImg(index,id) {
        this.uploadURLs.splice(index, 1);
        this.imgIDs.splice(index, 1);
       },
      delImg1(index,id) {
         this.copyuploadURLs.splice(index, 1);
         this.copyimgIDs.splice(index, 1);
         this.$emit('attachmentsID', this.copyimgIDs);
       }
    },
    props:['copyuploadURLs','attachments'],
    created() {
      let URLPth= process.env.INSIDE_ROOT;
      this.URLPth = URLPth;
      this.file = '/file/';
    },
    watch: {
      attachments (val) {
        if(val){
          this.copyimgIDs = val.split(',');
        }
        this.$emit('attachmentsID', this.copyimgIDs);
     }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
   .attachment_div {
    width: 60px;
    height: 60px;
    border: 1px dashed #e2e2e2;
    line-height: 53px;
    text-align: center;
    font-size: 40px;
    color: #e2e2e2;
  }
  .upload_img {
    width: 100%;
    height: 100%;
  }
  .file-remove {
    width: 13px;
    height: 13px;
    background: #333;
    text-align: center;
    line-height: 13px;
    border-radius: 50%;
    position: absolute;
    right: 0px;
    top: 0px;

  }

  .file-remove img{
    width: 13px;
    height: 13px;
  }


  .imgLi {
    float: left;
    width:30vw;
    height:30vw;
    margin-right: 2px;
    position: relative;
    display: inline-block;
    margin-top: 20px;

  }

  .imgLi:last-child{
    margin-right: 0;
  }



  .imgLi:last-child{
    margin-right: 0;
  }


  ul{
    width: 570px;
  }
</style>



