<template>
  <div class="edit-div"
       v-html="innerText"
       :contenteditable="canEdit"
       @focus="isLocked = true"
       @blur="isLocked = false"
       @input="changeText">
  </div>
</template>
<script>
  export default{
    name: 'editDiv',
    props: {
      value: {
        type: String,
        default: ''
      },
      canEdit: {
        type: Boolean,
        default: true
      }
    },
    data(){
      return {
        innerText: this.value,
        isLocked: false
      }
    },
    watch: {
      'value'(){
        if (!this.isLocked || !this.innerText) {
          this.innerText = this.value;
        }
      }
    },
    methods: {
      attachmentsID(childValue) {
        this.attachmentses = childValue.join();
      },
      changeText(){
        this.$emit('input', this.$el.innerHTML);
      }
    }
  }
</script>
<style scoped>
  .edit-div {
    width: 95%;
    min-height: 10vh;
    overflow: auto;
    word-break: break-all;
    outline: none;
    user-select: text;
    white-space: pre-wrap;
    text-align: left;
    border: 1px solid #e6e6e6;
    border-radius: 3px;
    padding: 2%;
    margin: auto;
 }
  .edit-div[contenteditable=true]{
    user-modify: read-write-plaintext-only;

  }
  .edit-div:empty:before {
    content: attr(placeholder);
    display: block;
    color: #e6e6e6;
  }



  .edit-div::-webkit-scrollbar{
    display: none;
  }

</style>



