<template>
	<div id="home">
		<router-view/>
		<div class="navigation">
			<router-link to="/home/fontpage">
				<div :class="{active:active1()}">
					<div><i class="icon iconfont icon-shouye"></i></div>
					<div></div>
				</div>
			</router-link>
			<router-link to="/home/wg">
				<div :class="{active:active2()}">
					<div><i class="icon iconfont icon-biaodan"></i></div>
					<div></div>
				</div>
			</router-link>
			<router-link to="/home/rpublicity">
				<div :class="{active:active3()}">
					<div><i class="icon iconfont icon-zhouqifuwushiduan"></i></div>
					<div></div>
				</div>
			</router-link>
				<!--<div @click="lout()">
					<div><i id="zhuxiao" class="icon iconfont icon-zhuxiao"></i></div>
					<div></div>
				</div>-->


      <router-link to="/accounts/index">
        <div :class="{active:active4()}">
          <div><i class="icon iconfont icon-geren"></i></div>
          <div></div>
        </div>
      </router-link>

		</div>
	</div>
</template>
<script>
import { MessageBox } from 'mint-ui';

export default {
	methods:{
    goRouter(){
      this.$router.push('/accounts/index');
    },
		active1(){
			return Object.is(this.$route.path , "/home/fontpage")
		},
		active2(){
			return Object.is(this.$route.path , "/home/wg")
		},
		active3(){
			return Object.is(this.$route.path , "/home/rpublicity")
		},
    active4(){
      return Object.is(this.$route.path , "/accounts/index")
    },
		lout(){
			MessageBox.confirm('?').then(action => {
				var _this = this;
				localStorage.setItem("ccc",null);
				_this.$router.push(_this.$route.query.redirect || '/signin')
			}).catch(action => {
			})
		}

	},
	components:{

	}
}
</script>

<style>
#home .navigation{
	display: flex;
	justify-content: space-between;
	padding: 0.5rem 5%;
	width: 100%;
	position: fixed;
	bottom: 0 ;
	text-align: center;
	font-size: 1rem;
	background-color: #fff;
	z-index: 0;
}
#home .icon{
	font-size: 2rem;
}

.active{
	color:#0b752b;
}

.notice_d{
	display: flex;
	height: 3rem;
	line-height: 3rem;
	margin: 2rem 0;
	justify-content: space-between;
	padding: 0 3%;
}
.notice_d_d1{
	display: flex;
	height: 3rem;
	line-height: 3rem;
}
.notice_d_d1 div{
	font-size: 1.1rem;
}

.notice_d_d1_d1{
	height: 3rem;
	width: 3rem;
	border-radius: 50%;
	background-color: #0b752b;
	color: #fff;
	text-align: center;
	line-height: 3rem;
}
#home #zhuxiao{
	font-size:1.8rem;
}
</style>



