<template>
	<div id="cg">

		<ptheader v-if="notice"></ptheader>
		<ptheader v-if="file"></ptheader>




		<ul class="concrete_ui"  v-infinite-scroll="loadMore" :infinite-scroll-disabled="loading" infinite-scroll-distance="10">
			<li v-for=" item in writt" class="concrete_li">
 				<router-link :to="{path:'/home/maintext',query:{id:item.id,types:item.types}}">
          <div class="adiv">
              <span>{{item.title.slice(0,15)}}</span>
              <span class="aspan">{{item.releaseTime}}</span>
          </div>
				</router-link>
     	</li>
		</ul>



		<div class="trytoload" v-if="trytoload">......</div>
		<div class="trytoload" v-if="loadover"></div>
	</div>
</template>
<script>
import ptheader from "@/components/header/ptheader";
export default {
	data(){
		return {
			loadover:false,
			trytoload:false,
			loading:true,
			writt:[],
			page:0,
			aaa:0,
      notice:false,
      file:false,
      type:''
		}
	},
	components:{
		ptheader
	},

	methods:{
    getUrlKey:function(name){
      return decodeURIComponent((new RegExp('[?|&]'+name+'='+'([^&;]+?)(&|#|;|$)').exec(location.href)||[,""])[1].replace(/\+/g,'%20'))||null;
    },
   loadMore(){
			var _this = this;
			_this.loading=true;
			this.$http.get('/inforelease/more?type='+this.type+'&offset=0&limit='+_this.page).then((res)=>{
				var allPage=res.total;

				_this.trytoload= false;
				if(_this.page<allPage){
					_this.page=_this.page;
					_this.writt=res.data;
          // _this.trytoload= true;
				}else{
					_this.trytoload= false;
					_this.loading = false;
					_this.loadover= true;
				}
				if(allPage<10){
          _this.trytoload= false;
				}
			})
		}
	},
  created() {

    let title = this.getUrlKey("id");

     if(title == 'file') {
        this.file = true;
     }

    if(title == 'notice') {
      this.notice = true;
    }


    this.type= this.getUrlKey("type");

  },
}
</script>

<style scoped>
  .concrete_li{
    border-bottom: 1px dashed #e8e8e8;
    text-align: left;
  }
  .concrete_li{
    display: block;
    width: 100%;
  }
  .adiv{
    width: 100%;
    display: block;
  }
  .aspan{
    float: right;
    display: inline-block;
  }
</style>



