<template>
	<div id="maintext">

    <ptheader v-if="notice"></ptheader>
    <ptheader v-if="file"></ptheader>

		<div class="maintext_content">
			<div class="mtitle"><b>{{mtitle}}</b></div>
			<div class="mtime">{{mtime}}</div>
      <p class="line"></p>
			<div class="marticle">
          {{marticle}}

        <ul class="mt50">
          <li></li>
          <li v-for="item in allFile">
            <a :href="item.pelativePath">{{item.name}}</a>

          </li>
        </ul>

      </div>






		</div>
	</div>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'
import ptheader from "@/components/header/ptheader";
export default {
	data(){
		return {
			marticle:"",
			mtitle:"",
			mtime:"",
			mdownload:"",
			ord:"",
      notice:false,
      file:false,
      allFile:[],
      URLPth: ''
		}
	},
	methods:{
   		getUrlKey:function(name){
			return decodeURIComponent((new RegExp('[?|&]'+name+'='+'([^&;]+?)(&|#|;|$)').exec(location.href)||[,""])[1].replace(/\+/g,'%20'))||null;
		}
	},
	components:{
		ptheader
	},
	created:function(){
    let URLPth= process.env.INSIDE_ROOT;
    this.URLPth = URLPth;
		let orderId=this.getUrlKey("id");
		this.ord =orderId;
		let _this = this;
		this.$http.get("/inforelease/get?id="+this.ord).then(res=>{
      _this.marticle = res.content;
      _this.mtitle = res.title;
      _this.mtime = res.releaseTime;
     	_this.allFile = res.attachmentFiles.map(val=>{
          val.pelativePath= URLPth+'/file/'+val.pelativePath;
            return val;
      });
    });


    let title = this.getUrlKey("types");
    if(title == '0') {
      this.notice = true;
    }
    if(title == '1') {
      this.file = true;
    }

  }
}
</script>

<style>
#maintext{
	margin-bottom:15vh;
}

#maintext_content{
	margin-top: 5vh;
}

.maintext_content_d1{
	font-size: 1rem;
	line-height: 2rem;
	margin:2vh 10%;
}
#maintext p{
	margin: 2vh 0;
}

.mtitle{
	text-align: center;
	font-size: 1.4rem;
	margin-top: 5vh;
	margin-bottom: 1vh;
	padding: 0vh 5% 0 5%;
}
.mtime{
	text-align: right;
	padding-right: 5%;
	font-size: 1.1rem;
	color:#666666;
}
.marticle{
	padding: 5vh 5%;
	letter-spacing: 1px;
	line-height: 2.5rem;
	font-size: 1.1rem;
	min-height: 50vh;
}
.mdownload{
	margin-top: 5vh;
	text-align: center;
	color: #0b752b;
}

.mtime{
  text-align: center;
}
  .line{
    width: 100%;
    height: 1px;
    border-bottom: 1px solid  #f1efef;
  }

  .mt50{
    margin-top: 50vw;
  }
</style>







