<template>
  <div id="pg">
    <ptheader></ptheader>
    <!--		<div v-if="pg9" class="wnewtext" @click="changepg()"></div>-->


    <div class="ig_d1">
      <label for="">*:</label>

      <input type="hidden"  v-model="dictCode"/>

      <select name="" v-model="pg1">

        <option v-for="item in pglist" v-bind:value="item.key">
          {{item.value}}</option>
      </select>


      <label for="">*:</label>
      <input v-model="pg2" type="text" @click="setpgtime()">


      <label for="">:</label>
      <contentedit   v-model="pg3"  class="areaDiv"></contentedit>
     <!-- <textarea v-model="pg3"></textarea>-->


      <label for="">:</label>
      <contentedit  v-model="pg4"  class="areaDiv"></contentedit>
     <!-- <input v-model="pg4" type="text">-->


      <label v-if="pgchange" for=""></label>
      <label v-else for=""></label>




      <attachment @attachmentsID="attachmentsID" :copyuploadURLs="copyuploadURLs"
                  :attachments="attachments"></attachment>




      <label for="">:</label>
      <contentedit   v-model="pg5"  class="areaDiv"></contentedit>

     <!-- <input v-model="pg5" type="text">-->


      <label for="">():</label>

      <input  v-model="pg6" type="text">




      <label v-if="pgchange" for=""></label>
      <label v-else for="">:<span v-html="pg7"></span></label>
       <label v-if="pgchange" for=""></label>

      <label v-else for="">:<span v-html="pg8"></span></label>



    </div>


    <div v-if="submitBtn" class="igsub" @click="pgsub()"></div>
    <div v-if="submitBtn" class="igtemporary" @click="saveFile()"></div>

    <div id="whole" v-if="wholeShow"></div>

  </div>
</template>

<script>
  import ptheader from "@/components/header/ptheader";
  import attachment from "@/components/attachment";
  import {MessageBox} from 'mint-ui';
  import contentedit from "@/components/contentedit";
  import { Toast } from 'mint-ui';
  export default {
    data() {
      return {
        pg1: [],
        pg2: "",
        pg3: "",
        pg4: "",
        pg5: "",
        pg6: "",
        pg7: "",
        pg8: "",
        pg9: false,
        pglist: [],
        pglist1: [],
        pgchange: false,
        pgid: "",
        basepath: "",
        imglist: [],
        pgimglist: [],
        copyimgIDs:[],
        attachmentses: '',
        copyuploadURLs: [],
        attachments: [],
        submitBtn: false,
        wholeShow: false,
        dictCode:''
      };
    },
    components: {
      ptheader, attachment,contentedit
    },
    methods: {

      attachmentsID(childValue) {


        this.attachmentses = childValue.join();

      },
        setpgtime() {
        this.$calendar.show({
          onOk: (date) => {
            this.pg2 = date;
          }
        });
      },
      pgsub() {
        let orderId = this.getUrlKey("id");
        var _this = this;
        var params = new FormData();
        params.append('dictCode', this.pg1);
        params.append('happenTime', this.pg2);
        params.append('content', this.pg3);
        params.append('place', this.pg4);
        params.append('checks', this.pg5);
        params.append('checkPeople', this.pg6);
        params.append('attachments', this.attachmentses);
        params.append('id', orderId);


         this.$http({
          method: 'post',
          url: 'prizepunish/update/submit',
          data:params
        }).then(res => {
           Toast("");
           _this.$router.push('/home/wg');
         });
      },
      saveFile() {
        let orderId = this.getUrlKey("id");
        var _this = this;
        var params = new FormData();
        params.append('dictCode', this.pg1);
        params.append('happenTime', this.pg2);
        params.append('content', this.pg3);
        params.append('place', this.pg4);
        params.append('checks', this.pg5);
        params.append('checkPeople', this.pg6);
        params.append('attachments', this.attachmentses);
        params.append('id', orderId);
        var vConsole = new VConsole();
        console.log(this.attachmentses);
        this.$http({
          method: 'post',
          url: 'prizepunish/update/temporary ',
          data:params
        }).then(res => {
          Toast("");
          _this.$router.push('/home/wg');
        });
      },
       getUrlKey: function (name) {
        return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null;
      },


      init() {
        let _this = this;
        let params = new URLSearchParams();
        params.append('code', 'event_type,examine');
        this.$http({
          method: 'post',
          url: '/common/constant',
          data: params
        }).then(function (res) {
          _this.pglist = res.event_type;
          _this.pglist1 = res.examine;

        });
      }
    },
    created() {


      this.init();
      let orderId = this.getUrlKey("id");
      let _this = this;
      _this.$http.get("/prizepunish/detail?id=" + orderId).then(res => {
       _this.pg6 = res.checkPeople;
        _this.pg3 = res.content;
        _this.pg2 = res.happenTime;
        _this.pg4 = res.place;
        _this.pg5 = res.checks;//
        _this.pg8 = res.examinePeople;

        _this.copyuploadURLs = res.attachmentFiles;
        _this.attachments =  res.attachments;

        if (res.operationState == '0') {
          _this.submitBtn = true;
          _this.wholeShow = false;
        } else {
          _this.submitBtn = false;
          _this.wholeShow = true;
        }
        for (let j = 0; j < _this.pglist1.length; j++) {
          if (res.examineState == _this.pglist1[j].key) {
            _this.pg7 = _this.pglist1[j].value;
          }
        }


        //
          for (let j = 0; j < _this.pglist.length; j++) {
            if (res.dictCode == _this.pglist[j].key) {
              _this.pg1 = _this.pglist[j].key;
            }
          }

        _this.imglist.forEach((item, index) => {
          _this.imglist1 = _this.imglist1.concat("data:image/jpeg;base64," + item.imgName);
          _this.pgimglist = _this.pgimglist.concat(item.imgName);
        });

        if (res.prizepunish.operationState == 0) {
          _this.pg9 = true;
        }
      });
    },
  };
</script>

<style>
  .ig_d1 {
    margin: 3rem 3% 0 3%;
  }

  #pg input {
    font-size: 1.1rem;
  }

  #pg select {
    font-size: 1.1rem;
  }

  #pg label {
    font-size: 1.1rem;
  }

  .ig_d1 label {
    display: block;
    margin: 2vh 0;
  }

  .ig_d1 input {
    display: block;
    height: 2.5rem;
    line-height: 2.5rem;
    border: 1px solid #e6e6e6;
    border-radius: 5px;
    width: 95%;
    margin: 0 auto;
    padding-left: 2%;
  }

  .ig_d1 select {
    display: block;
    height: 2.5rem;
    line-height: 2.5rem;
    border: 1px solid #e6e6e6;
    border-radius: 5px;
    width: 95%;
    margin: 0 auto;
    padding-left: 2%;
  }

  .igsub {
    width: 90%;
    margin: 0 auto;
    text-align: center;
    margin-top: 2rem;
    height: 3rem;
    line-height: 3rem;
    font-size: 1.3rem;
    background-color: #0b752b;
    color: #fff;
    border-radius: 5px;
  }

  .igtemporary {
    width: 90%;
    margin: 0 auto;
    text-align: center;
    margin-top: 2rem;
    height: 3rem;
    line-height: 3rem;
    font-size: 1.3rem;
    background-color: #d2d2d2;
    color: #111;
    border-radius: 5px;
    margin-bottom: 5vh;
  }

  #pg textarea {
    width: 95%;
    resize: none;
    display: inline-block;
    margin-left: 2.5%;
    height: 7rem;
    padding: 0.3rem 2%;
  }

  #pg .pglastinput {
    margin-bottom: 10vh;
  }

  #pg .wnewtext {
    position: absolute;
    height: 10vh;
    line-height: 10vh;
    right: 4%;
    top: 0.1rem;
    font-size: 1.3rem;
    color: #fff;
  }

  .pgimg {
    width: 100%;
  }

  .pgimg img {
    width: 30%;
    margin-left: 3%;
    max-height: 20vh;
  }

  #pg .sup_din1 {
    width: 15vw;
    height: 15vw;
    border: 1px dashed #e6e6e6;
    margin-top: 5vw;
    margin-left: 5%;
    position: relative;
    font-size: 3rem;
    text-align: center;
    line-height: 15vw;
    color: #e6e6e6;
  }

  #pg #sup_in1 {
    display: inline-block;
    width: 100%;
    height: 100%;
    opacity: 0;
    position: absolute;
    left: 0;
    margin: 0;
    padding: 0;
    border: none;
  }

  #pg #previewimg {
    width: 100%;
    margin-top: 2rem;
  }

  #pg #previewimg .preview {
    width: 30%;
    display: inline-block;
    height: 13vh;
    margin-right: 3%;
    margin-bottom: 2vh;
    position: relative;
  }

  #pg .previegiveup {
    position: absolute;
    top: -2vw;
    right: -2vw;
    text-align: center;
    border: 1px solid #999999;
    height: 5vw;
    width: 5vw;
    background-color: #e5e5e5;
    color: #999999;
    opacity: 0.5;

    border-radius: 100%;
  }

  #pg #previewimg .preview img {
    width: 100%;
    height: 100%;
  }

  textarea {
    border: 1px solid #e6e6e6;
    border-radius: 5px;
  }
  #whole{
    width: 100%;
    height: 100%;
    background: transparent;
    position: absolute;
    z-index: 9999;
    top:10vh;
  }
</style>



