 <template>
	<div id="rpublicity">
		<nptheader class="rpublicity_header"></nptheader>
		<div class="rnewtext" @click="new1gesr()"></div>
		<div class="wg_d1">
			<span class="selectbycytitle"></span>
			<span @click="r_ul1()" class="selectbycyspan">{{rtc}}</span>
			<div id="triangle_down"></div>
		</div>
		<div class="wg_d2">
			<selfem v-if="selfemshow"></selfem>
			<ul>
				<li v-for="item in rpublicitys2">
					<router-link :to="{path:'/pay',query:{id:item.id}}">
					<div class="rpublicityd1">
						<span class="rpublicityd1_s1">: &nbsp;&nbsp;</span>   <span> {{item.assessCycleName}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					</div>
					<div class="rpublicityd2">
						<span class="rpublicityd1_s1">: &nbsp;&nbsp;</span>
             <span>{{item.selfAssGrade}}</span>
					</div>
					<div class="rpublicityd1">
						<span class="rpublicityd1_s1">: &nbsp;&nbsp;</span> <span>{{item.departGrade}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					</div>
					<div class="rpublicityd2">
						<span class="rpublicityd1_s1">: &nbsp;&nbsp;</span> <span>{{item.unitAssGrade}}</span>
					</div>
					<div class="rpublicityd1">
						<span class="rpublicityd1_s1">:&nbsp;&nbsp; </span> <span> {{item.operateState}}</span>
					</div>
					</router-link>
				</li>
			</ul>
		</div>




   <mt-popup
      v-model="rtcshow"
      position="bottom">
      <ul v-show="rtcshow" class="rul1">
        <li @click="rtcshow=!rtcshow" style="text-align: right;padding-right: 10px;margin: 0px;justify-content: flex-end;"></li>
        <li class="rul1_l2">
          <span></span>
          <span></span>
        </li>

        <li v-for="item in rlist1" @click="selectbycy(item.id)">
          <span class="rul1_s1">{{item.cycleName}}</span>
          <span class="rul1_s2">{{changetime(item.assessEndTime)}}</span>
        </li>









      </ul>
    </mt-popup>
  </div>
</template>
<script>

import Vue from 'vue'
import axios from 'axios'
import nptheader from "@/components/header/nptheader";
import selfem from "@/components/children/grandson2/selfem";
import { MessageBox } from 'mint-ui';

export default {
	data(){
		return {
			rtc:"",
			rtcshow:false,
			selfemshow:true,
			rlist1_value:"",
			rpublicity_list:"",
			rpublicity_list2:[],
			rpublicity_list3:"",
			rpublicitys1:"",
			rpublicitys2:[],
			rpublicitys3:"",
			rpublicitys4:"",
			rlist1:[],
			operateStatelist1:"",
			selfAssGradelist1:"",
			departGradelist1:"",
			unitAssGradelist1:"",
      operateState:[]
		}
	},
	components:{
		nptheader,selfem
	},
	methods:{
		changetime(e){
			var newTime = new Date(e);
			var m = newTime.getMonth()+1
			var y = newTime.getFullYear()
			var d = newTime.getDate()
			if(m<10&&d<10){
				return y+"-0"+m+"-0"+d
			}
			if(m<10&d>9){
				return y+"-0"+m+"-"+d
			}
			if(m>9&d<10){
				return y+"-"+m+"-0"+d
			}
			else{
				return y+"-"+m+"-"+d
			}

		},
		r_ul1(){
			this.rtcshow =true;
		},
		selectbycy(id){


      var params = new FormData();
      params.append('assessCycleId',id);
      this.rpublicitys2 = [];
      let _this = this;
       this.$http({
        method: 'post',
        url: '/assess/cycle/personal/find',
        data:params
      }).then(res => {
         _this.rtcshow =false;
         _this.rpublicitys2 = res.data;

           for(let i = 0; i < _this.rpublicitys2.length; i++){
           for(let j  = 0; j < _this.rpublicity_list2.length; j++){
             if( _this.rpublicitys2[i].selfAssGrade == _this.rpublicity_list2[j].key){
               _this.rpublicitys2[i].selfAssGrade = _this.rpublicity_list2[j].value;

             }

             if( _this.rpublicitys2[i].departGrade == _this.rpublicity_list2[j].key){
               _this.rpublicitys2[i].departGrade = _this.rpublicity_list2[j].value;

             };

             if( _this.rpublicitys2[i].unitAssGrade == _this.rpublicity_list2[j].key){
               _this.rpublicitys2[i].unitAssGrade = _this.rpublicity_list2[j].value;

             }
           }
         };
         for(let a = 0; a < _this.rpublicitys2.length; a++){
           for(let q  = 0; q < _this.operateState.length; q++){
             if( _this.rpublicitys2[a].operateState == _this.operateState[q].key){
               _this.rpublicitys2[a].operateState = _this.operateState[q].value;
             }
           }
         };




      })














		},
		new1gesr(){
			this.$router.push(this.$route.query.redirect || '/wexamination')
		},
    init() {

      let _this = this;
      let params = new URLSearchParams();
      params.append('code', 'AppAssGrades,operate-state');
      this.$http({
        method: 'post',
        url: '/common/constant',
        data: params
      }).then(function (res) {
        _this.rpublicity_list2 = res.AppAssGrades;
        _this.operateState = res['operate-state'];
      });
    }




	},
	created(){
	  this.init();
	  let _this = this;
		this.$http.get("/assess/cycle/personal/find").then(res=>{
          _this.rpublicitys2 = res.data;


       for(let i = 0; i < _this.rpublicitys2.length; i++){
        for(let j  = 0; j < _this.rpublicity_list2.length; j++){
          if( _this.rpublicitys2[i].selfAssGrade == _this.rpublicity_list2[j].key){
            _this.rpublicitys2[i].selfAssGrade = _this.rpublicity_list2[j].value;

           }

          if( _this.rpublicitys2[i].departGrade == _this.rpublicity_list2[j].key){
            _this.rpublicitys2[i].departGrade = _this.rpublicity_list2[j].value;

          };

          if( _this.rpublicitys2[i].unitAssGrade == _this.rpublicity_list2[j].key){
            _this.rpublicitys2[i].unitAssGrade = _this.rpublicity_list2[j].value;

          }
        }
      }






      for(let a = 0; a < _this.rpublicitys2.length; a++){
        for(let q  = 0; q < _this.operateState.length; q++){
          if( _this.rpublicitys2[a].operateState == _this.operateState[q].key){
              _this.rpublicitys2[a].operateState = _this.operateState[q].value;
          }
        }
      };







      this.$http.get("/assess/cycle/select/self").then(res => {
        this.rlist1 = res.data;
      });







    });



	},
}
</script>
<style scoped>
  .mint-popup-bottom{
    width: 100%;
  }
</style>














