<template>
	<div id="sigin">
		<div class="register_header">
				<img src="static/img/logo.png">
			<span></span>
		</div>
    <div class="internet">
        <span @click="changeNet(1)" :class="{'chooseActive':isActive==1}"></span>
        <span @click="changeNet(2)" :class="{'chooseActive':isActive==2}"></span>
    </div>
			<div class="sigin_div">
				<label for="">&nbsp;&nbsp;&nbsp;</label><input type="text" v-model="truephone">
			</div>
			<div class="sigin_notice">
			</div>
			<div class="sigin_div">
				<label for="">&nbsp;&nbsp;&nbsp;</label><input type="password" name="" id="" v-model="sign_pasword">
			</div>
			<div class="sigin_notice"></div>
         <button class="sup_button" id="sigin_register" type="button" @click="gogogo()"></button>
	</div>
</template>
<script>
import crypto from 'crypto'
import { MessageBox } from 'mint-ui';
import axios from 'axios'
export default {
	data(){
		return {
			truephone:"ksrmyy4199",
			sign_pasword:"000000",
			sign_check:null,
			gotosign:false,
      isActive:0,
		}
	},
	methods:{
    changeNet(data) {
      this.isActive = data;
      localStorage.clear();
      localStorage.setItem('INTERNET',data);
      var Net= localStorage.getItem('INTERNET');
      if(Net == 1) {
        axios.defaults.baseURL =  process.env.INSIDE_ROOT;

      }else if(Net == 2){

        axios.defaults.baseURL =  process.env.OUTSIDE_ROOT;

      };

    },
		gogogo(){
      var Net= localStorage.getItem('INTERNET');
      if(!Net) {
        MessageBox.alert("");
        return false;
      };
			var _this= this;
      let aa =  new FormData();
      aa.append('username',this.truephone);
      aa.append('password',this.sign_pasword);
      this.$http.post('/login',aa).then( response => {
       if(response){
          var now = new Date();
          var ccc={
             'token':response.token,
             'userId':response.userId
          };
        //  var userID = response.userSession.userId;
        //  localStorage.setItem("userId",userID);
          localStorage.setItem("ccc",JSON.stringify(ccc));
          localStorage.setItem("token",response.token);
          this.$router.push(this.$route.query.redirect || '/home/fontpage');
        }else if(response.status ==2){
          MessageBox.alert('/!').then(action=>{}).catch(error=>{})
        }


      });

		}
	},
	created(){
	//  localStorage.clear();

   // localStorage.setItem('INTERNET','1');


    if(JSON.parse(localStorage.ccc)){
      this.$router.push('/home/fontpage');
    }


    /*
		if(localStorage.ccc){
			var ddd= JSON.parse(localStorage.ccc);
			if(ddd.createdtime && (new Date().getTime() - ddd.createdtime < 1800000) && ddd.id) {
				this.$router.push('/home/fontpage');
			} else {
				localStorage.removeItem("ccc");
			}
   }
*/




  /*  this.truephone = ddd.username;
    this.sign_pasword = ddd.password;*/

	},
}

</script>
<style scoped>
.register_header img{
	width: 50%;
}
.register_header{
	padding-top: 3rem;
	width: 100%;
	height:27rem;
	font-size: 1.6rem;
	text-align: center;
	color: #fff;
	margin-bottom:1rem;
	background:linear-gradient(to right, #0b752b,#076322);
}

.register_header span{
	margin-top: 1.5rem;
	display: block;
	color: #fff;
	width: 100%;
	text-align: center;
}

#sigin .sigin_div input{
  border-bottom: 1px solid #076523;
}
.internet{
  text-align: center;
}
  .internet span{
    display: inline-block;
    width: 4rem;
    height: 2rem;
    line-height: 1.8rem;
    text-align: center;
    border:1px solid #0b752b;
    margin-right: 2rem;
    border-radius: 5px;
  }
  .chooseActive{
    background: #0b752b;
    color: white;
  }
</style>



