<template>
	<div id="interview1">
		<div>
			<div class="harticle_d_d1"><span></span><span class="hamore"><router-link to="/cg?id=file&type=1">>></router-link></span></div>
			<ul>
				<li v-for="item in inlist.slice(0,5)" class="Wexamination_li" v-if="item.types ==1">
					<router-link :to="{path:'/home/maintext',query:{id:item.id,types:1}}">
						<div class="ellipsis">{{item.title}}</div>
						<div class="frontnotice_li_d2">{{item.releaseTime}}</div>
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
export default {
	data(){
		return {
			inlist:""
		}
	},

	created(){
    this.$http.get("/inforelease/app/info/index").then(res=>{
     this.inlist=res;
		})
	}
}
</script>

<style>
#interview1{
	border-bottom: 1px solid #e6e6e6;
	border-top: 0;
	box-shadow: 0 5px 5px #e6e6e6;
	margin: 0 1% 3vh 1%;
	margin-bottom: 11vh;
}

#interview ul span{
	font-size:0.9rem;
}
  .ellipsis{
    width: 17rem;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    display: inline-block;
    height: 2rem;
    float: left;
  }
  .frontnotice_li_d2{
    float: right;
  }
</style>



