<template>
  <div id="wg" class="content">
    <nptheader class="wg_header"></nptheader>
    <div class="wnewtext" style="z-index:99999999999999999999999;" @click="new1gesn()"></div>
    <div class="wg_d1">
      <span @click="kind()">{{wgspan1}}</span>
      <span @click="status()">{{wgspan2}}</span>
    </div>
    <div class="wg_d2">
      <regularuld v-if="wgulishow1"></regularuld>
      <regularul v-if="wgulishow4"></regularul>
      <ul>
        <li v-for="item in wg_list">
          <router-link :to="{path:'/pg',query:{id:item.id}}">
            <div class="wg_d2_d1"><i class="icon iconfont icon-chaxun"></i></div>
            <div class="wg_d2_d2">
              <div class="wg_d2_u_l_d1">
                <span class="wg_d2_u_l_d1_s1">{{item.dictCode}}</span>
                <span class="wg_d2_u_l_d1_s2">{{item.happenTime}}</span>
               </div>
              <div class="wg_d2_u_l_d2">{{item.content}}</div>

            </div>
          </router-link>
          <div class="del"> <span v-if="item.operationState==0" @click="del(item.id)"></span></div>
        </li>
      </ul>
    </div>


    <mt-popup
      v-model="kindShow"
      position="bottom">
      <ul  class="wg_d1_ul1">
        <li @click="kindShow=!kindShow" class="text-right"></li>
        <li @click="selectKind(item.key)" v-for="(item,index) in wglist1" :value="item.key">{{item.value}}</li>
      </ul>
    </mt-popup>



    <mt-popup
      v-model="statusShow"
      position="bottom">
      <ul  class="wg_d1_ul2">
        <li @click="statusShow=!statusShow" class="text-right"></li>
        <br/>
        <br/>
        <li @click="selectbyCstate(item.key)" v-for="item in wglist2" :value="item.key">{{item.value}}</li>
      </ul>
    </mt-popup>



  </div>
</template>
<script>
  import nptheader from "@/components/header/nptheader";
  import regularul from "@/components/ul/regularul";
  import regularuld from "@/components/ul/regularuld";
  import { MessageBox } from 'mint-ui';
  export default {
    data() {
      return {
        popupVisible:false,
        wg1: "",
        wg_show1: false,
        wg_show2: false,
        wglist1: [],
        wglist2: '',
        wg_list: "",
        wg_list1: "",
        wg_list2: "",
        wg_list3: "",
        wg_list4: "",
        page: 0,
        wgulishow1: true,
        wgulishow2: false,
        wgulishow3: false,
        wgulishow4: false,
        wgspan1: "",
        wgspan2: "",
        wgspan3: "",
        kindShow: false,
        statusShow: false
      }
    },
    components: {
      nptheader,
      regularul,
      regularuld
    },
    computed: {

    },
    methods: {
      kind() {
          this.kindShow = true;
      },
      selectKind(type) {

        var params = new FormData();
            params.append('dictCode',type);
         this.wg_list = [];

        let _this = this;
        this.$http({
          method: 'post',
          url: '/prizepunish/find',
          data:params
        }).then(res => {
          _this.kindShow = false;
          _this.wg_list = res.data;

          for(let i = 0; i < _this.wg_list.length; i++){
            for(let j  = 0; j < _this.wglist1.length; j++){
              if( _this.wg_list[i].dictCode == _this.wglist1[j].key){
                _this.wg_list[i].dictCode = _this.wglist1[j].value;
              }
            }
          }
        })
      },
      selectbyCstate(state) {
        var params = new FormData();
        params.append('examineState',state);
        this.wg_list = [];

        let _this = this;
        this.$http({
          method: 'post',
          url: '/prizepunish/find',
          data:params
        }).then(res => {
          _this.statusShow = false;
          _this.wg_list = res.data;
          for(let i = 0; i < _this.wg_list.length; i++){
            for(let j  = 0; j < _this.wglist1.length; j++){
              if( _this.wg_list[i].dictCode == _this.wglist1[j].key){
                _this.wg_list[i].dictCode = _this.wglist1[j].value;
              }
            }
          }
       })
      },
      status() {
        this.statusShow = true;
      },
      del(itemId) {
        let _this = this;
        MessageBox.confirm('?').then( res => {
          this.$http.get('/prizepunish/delete?id='+itemId).then( res=> {
            MessageBox.alert("");
             this.$http.get('/prizepunish/find').then(res => {
              _this.wg_list = res.data;
              for(let i = 0; i < _this.wg_list.length; i++){
                for(let j  = 0; j < _this.wglist1.length; j++){
                  if( _this.wg_list[i].dictCode == _this.wglist1[j].key){
                    _this.wg_list[i].dictCode = _this.wglist1[j].value;
                  }
                }
              }
            });

           });
        })
      },
       new1gesn() {
        this.$router.push(this.$route.query.redirect || '/ig')
      },
      init(){
        let _this = this;
        let params = new URLSearchParams();
        params.append('code', 'event_type,examine');
        this.$http({
          method: 'post',
          url: '/common/constant',
          data  : params
        }).then(function (res) {
           _this.wglist1 = res.event_type;
           _this.wglist2 = res.examine;

        })
      },
    },
    created() {
      let _this = this;
      this.init();
      this.$http.get('/prizepunish/find').then(res => {
          _this.wg_list = res.data;
         for(let i = 0; i < _this.wg_list.length; i++){
          for(let j  = 0; j < _this.wglist1.length; j++){
            if( _this.wg_list[i].dictCode == _this.wglist1[j].key){
               _this.wg_list[i].dictCode = _this.wglist1[j].value;
             }
           }
        }
     });
   },
  }
</script>

<style>
  #wg {
    margin-bottom: 20vh;
    font-size: 1.1rem;
  }
 #wg .wg_d1 {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 5%;
    text-align: center;
    position: fixed;
    z-index: 10000;
    background-color: #fff;
    top: 10vh;
    z-index: 9999999999;
  }

  #wg .wg_d1 span {
    display: inline-block;
    width: 33%;
    height: 4rem;
    line-height: 4rem;
    overflow: hidden;
    font-size: 1.1rem;

  }

  #wg .wg_d1_ul1 li {
    margin-left: 10%;
    line-height: 3rem;
    text-align: center;
  }

  .mint-popup-bottom{
    width: 100%;
    height: 20rem;
    overflow-y: scroll;
  }

  #wg .wg_d1_ul1 {
    width: 100%;
    font-size: 1.1rem;
   }

  #wg .wg_d1_ul2 li {
    margin-left: 10%;
    height: 3rem;
    line-height: 3rem;
  }

  #wg .wg_d1_ul2 {
    font-size: 1.1rem;
    width: 100%;
    margin-top: 2vh;
    position: fixed;
    background-color: #fff;
    z-index: 10000;
  }

  #wg .wg_d1_ul3 li {
    margin-left: 10%;
    height: 3rem;
    line-height: 3rem;
  }

  #wg .wg_d1_ul3 {
    margin-top: 2vh;
    width: 100%;
    font-size: 1.1rem;
    position: fixed;
    background-color: #fff;
    z-index: 10000;
  }

  #wg .wg_d1_ul2 li:hover {
    color: #0b752b;
  }

  #wg .wg_d1_ul1 li:hover {
    color: #0b752b;
  }

  #wg .wg_d2 {
    margin-top: 8.3rem;

  }

  #wg .wg_d2 li {
    border-bottom: 0.1px solid #f3f3f3;
    position: relative;
    width: 100%;
    background: #ffffff;
 }

  #wg .wg_d2 li a {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e6e6e6;
    padding-bottom: 2vh;
    padding-top: 2vh;
  }

  #wg .wg_d2_u_l_d1 {
    margin: 2vh 0;
    display: flex;
    justify-content: space-between;
    margin-right: 5%;
  }

  #wg .wg_d2_u_l_d2 {
    margin: 1vh 0;
    color: #666666;
  }

  #wg .wg_bg {
    height: 90vh;
    width: 100%;
    background: linear-gradient(#ffffff, #e6e6e6ccc);
    position: fixed;
    top: 10vh;
    z-index: 10;
  }

  #wg .wg_d2_d1 {
    width: 4rem;
    margin-right: 5%;
    margin-top: 1rem;
    height: 4rem;
    line-height: 4rem;
    text-align: center;
    color: #fff;
    background-color: #0b752b;
    border-radius: 100%;
    font-size: 2rem;
  }

  #wg .wg_d2_d2 {
    width: 80%;
  }

  #wg .wg_d2_u_l_d1_s1 {
    font-weight: bolder;
  }

  #wg .wg_d2_u_l_d1_s2 {
    color: #999999;
  }

  #wg .wg_header {
    position: fixed;
    top: 0;
    z-index: 9999999999999;
  }

  #wg .wnewtext{
    z-index: 999999999999999999999;
  }

  #wg .wnewtext {
    position: fixed;
    height: 10vh;
    line-height: 10vh;
    right: 4%;
    top: 0.1rem;
    font-size: 1.3rem;
    color: #fff;
    z-index: 999999999999999999999;
  }



  .wg_d2_u_l_d1_s1{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 149%;
    display: inline-block;
    height: 20px;
  }


  #wg .wg_d2_u_l_d1_s2{
    width: 80%;
  }

  .del span{
    float: right;
    font-size: 14px;
    border: 1px solid #e5e5e5;
    border-radius: 3px;
    display: inline-block;
    width: 3.5rem;
    height: 1.7rem;
    line-height: 1.4rem;
    text-align: center;
    color: #0b752b;
  }
  .text-right{
    text-align: right!important;
    float: right;
    height: 3rem;
    line-height: 3rem;
    width: 100%;
    padding-right: 1rem;
  }

  .del{
    position:absolute;
    right:1vw;
    top: 1vw;
  }
  .wg_d2,#regularuld{
    background: white;
  }
</style>



