import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home/home.vue'
import fontpage from '../components/home/fontpage.vue'
import rpublicity from '../components/children/grandson/rpublicity.vue'
import wexamination from '../components/children/grandson/wexamination.vue'
import interview from '../components/children/grandson/interview.vue'
import cg from '../components/children/grandson3/cg.vue'
import pay from '../components/children/grandson3/pay.vue'
import ptheader from "../components/header/ptheader.vue"


import wg from '../components/children/grandson4/wg.vue'
import pg from '../components/children/grandson4/pg.vue'
import ig from '../components/children/grandson4/ig.vue'
import maintext from '../components/children/article/maintext.vue'
import signin from '../components/Signandregister/signin.vue'

Vue.use(ptheader);
Vue.use(Router);

const accounts = [


];

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/signin',
    },
    {
      path:'/signin',
      component:signin,
    },
    {
      path:'/pg',
      component:pg,
    },
    {
      path:'/ig',
      component:ig,
    },
    {
      path:'/cg',
      component:cg,
    },
    {
      path:'/pay',
      component:pay,
    },
    {
      path:'/wexamination',
      component:wexamination,
    },
    {
      path:'/interview',
      component:interview,
    },

    {
      path:'/home',
      component:home,
      children:[{
          path:'fontpage',
          component:fontpage,
        },
        {
          path:'rpublicity',
          component:rpublicity,
        },
        {
          path:'wg',
          component:wg,
        },
        {
          path:'maintext',
          component:maintext, //maintext
        }

      ]
    },
    {
      path: '/accounts/index',
      alias:'/accounts',
      name: '',
      component: () => import('@/views/accounts')
    },
    {
      path: '/accounts/awards',
      name: '',
      component: () => import('@/views/accounts/awards')
    },
    {
      path: '/accounts/punish',
      name: '',
      component: () => import('@/views/accounts/punish')
    },
    {
      path: '/accounts/medical',
      name: '',
      component: () => import('@/views/accounts/medical')
    },
    {
      path: '/accounts/resume',
      name: '',
      component: () => import('@/views/accounts/resume')
    }

  ]

})





