<template>
	<div id="hheader">
		<div class="hheader_d1"></div>
		<div class="hheader_img"><img src="static/img/ftitle.png" alt=""></div>
	</div>
</template>

<script>
export default {
	data(){
		return {
			hevesign:true
		}
	},
	created:function () {
		if(localStorage.ccc == undefined){
			this.hevesign = true
		}else{
			this.hevesign = false
		}
	}
}
</script>

<style>
#hheader{
	height:30vh;
	line-height: 50vh;
	width: 100%;
	color: #111;
	display:flex;
	justify-content: space-between;
	position: relative;
	top: 0;
	z-index: 1000;
	font-size: 1.2rem;
	margin-bottom:3vh;
}
#hheader a{
	color: #fff;
	font-size: 1rem;
}
#hheader div{	
	position: absolute;
}


.hheader_img{
	width: 100%;
	height: 100%;
	z-index: 100;
}
.hheader_d1{
	height: 30vh;
	z-index: 10000;
	padding-left: 5%;
	font-size: 1.4rem;
	width: 100%;
}
.hheader_img img{
	width: 100%;
	height: 100%;
	z-index: 100;
}



</style>



