        }).then(res => {
          var _this = this;
          if (res.data.status == 0) {
            MessageBox.alert("!").then(action => {
              _this.$router.push(_this.$route.query.redirect || '/home/rpublicity');
            });
          } else if (res.data.status == 1) {
            MessageBox.alert("!").then(action => {
              _this.$router.push(_this.$route.query.redirect || '/home/rpublicity');
            });
          }
          console.log(res);
        });
      },*/
   /*   paykeep() {
        this.$http.post("/person/cyc/app/save?JSESSIONID=" + this.pid, {
          worksummary: this.cycle6,
          selfAssOpinion: this.cycle7,
          selfAssGrade: this.cycle8,
