      setpaytime2() {
        this.$calendar.show({
          onOk: (date) => {

            this.cycle9 = date;
          }
        });
      },
      getUrlKey: function (name) {
        return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null;
      },

      init() {
        let _this = this;
        let params = new URLSearchParams();
        params.append('code', 'AppAssGrades,operate-state');
        this.$http({
          method: 'post',
          url: '/common/constant',
          data: params
        }).then(function (res) {
          _this.cyclelist1 = res.AppAssGrades;
          _this.operateState = res.operateState;
       });
      },
      changetime(e){
        var newTime = new Date(e);
        var m = newTime.getMonth()+1;
        var y = newTime.getFullYear();
        var d = newTime.getDate();
        if(m<10&&d<10){
          return y+"-0"+m+"-0"+d
        }
        if(m<10&d>9){
          return y+"-0"+m+"-"+d
        }
        if(m>9&d<10){
          return y+"-"+m+"-0"+d
        }
        else{
          return y+"-"+m+"-"+d
        }

      }
    },
    created() {
      this.init();
      let _this = this;
      var ddd = JSON.parse(localStorage.ccc);
      this.pid = ddd.id;
      //
      this.$http.get("/assess/cycle/select/self").then(res => {
        this.cyclelist2 = res.data;
      });
      let orderId = this.getUrlKey("id");
      localStorage.orderId = orderId;
      this.$http.get("/assess/cycle/personal/get?id=" + orderId).then(res => {
        this.cycle5 = res.assessCycleId;
        this.cycle6 = res.workSummary;
        this.cycle7 = res.selfAssOpinion;
        this.cycle8 = res.selfAssGrade;

        if(res.operateState == 0) {
          this.submitBtn = true;
          this.wholeShow = false;
        } else {
          this.submitBtn  = false;
          this.wholeShow = true;
        }
        this.cycle9 = this.changetime(res.selfAssTime);
        this.cycle10 = res.assessedSign;
        this.payid = res.id;



        _this.departOpinion = res.departOpinion;
         _this.depAssessorSign = res.depAssessorSign;
        _this.departAssTime = _this.changetime(res.departAssTime);
        _this.unitGroupOpinion = res.unitGroupOpinion;
        _this.unitAssessorSign = res.unitAssessorSign;
        _this.unitAssTime = _this.changetime(res.unitAssTime);
        _this.assessedConfirmSign = res.assessedConfirmSign;
        _this.confirmedTime =  _this.changetime(res.confirmedTime);


        for(let i = 0; i < _this.cyclelist1.length; i++){
          if(res.departGrade == _this.cyclelist1[i].key){
            _this.departGrade = _this.cyclelist1[i].value;
           }
        }


        for(let i = 0; i < _this.cyclelist1.length; i++){
          if(res.unitAssGrade == _this.cyclelist1[i].key){
            _this.unitAssGrade = _this.cyclelist1[i].value;
          }
        }



        for(let i = 0; i < _this.cyclelist1.length; i++){
          if(res.confirmedResult == _this.cyclelist1[i].key){
             _this.confirmedResult = _this.cyclelist1[i].value;
           }
        }







      });
   },
  };
</script>


