<template>
	<div id="Wexamination">
		<ptheader></ptheader>
		<ul class="concrete_ui"  v-infinite-scroll="loadMore" :infinite-scroll-disabled="loading" infinite-scroll-distance="10">
			<li v-for=" item in writt" class="concrete_li">
				<router-link :to="{path:'/home/maintext',query:{id:item.id}}">
					<span>{{item.title.slice(0,15)}}</span>
				</router-link>
				<div>
					<span>{{item.releaseTime}}</span>
				</div>
			</li>
		</ul>
		<div class="trytoload" v-if="trytoload">......</div>
		<div class="trytoload" v-if="loadover"></div>
	</div>
</template>
<script>
  import ptheader from "@/components/header/ptheader";
export default {
	data(){
		return {
			loadover:false,
			trytoload:false,
			loading:true,
			writt:[],
			page:10,
			aaa:0,
		}
	},
	components:{
		ptheader
	},
	methods:{
		loadMore(){
			let _this = this;
			_this.loading=true;
      _this.$http.get('/console/infore/app/list?id=0&offset=0&limit='+_this.page).then((res)=>{
        var allPage=res.total;
        this.trytoload= false;
        if(_this.page<=allPage+3){
          _this.page=_this.page+3;
          _this.trytoload= true;
          _this.writt=res.data;
        }else{
          _this.trytoload= false;
          _this.loading = false;
          _this.loadover= true;
        }
        if(allPage<10){
          this.trytoload= false;
        }
      })
		}
	},
}
</script>

<style>
#Wexamination{
	margin-bottom: 10vh;
}

#Wexamination button{
	height: 100px;
	width: 100%;
}
</style>



